import * as React from 'react';
import { RenderNodeProps } from '@ali/4ever-cangjie';
import { PlaceholderWrapper } from '@ali/4ever-component';

interface DeletedProps {
  attributes: RenderNodeProps['attributes'];
}

const Deleted: React.FC<DeletedProps> = (props) => {
  const { attributes, children } = props;
  return (
    <PlaceholderWrapper data-testid="refblock-deleted" {...attributes}>
      {children}
    </PlaceholderWrapper>
  );
};

export default Deleted;
